import { Injectable } from '@angular/core';
import {BehaviorSubject} from 'rxjs';
import {LighthouseBrandListDisplayItem} from '../models/lighthouse/lighthouse-source-search';
import {OrganizationConnectionCallbackEvent} from '../models/organization-connection-callback-event';
import {ComponentNavigationEvent} from '../models/component-navigation-event';

@Injectable({
  providedIn: 'root'
})
export class MessageBusService {

  //this subject is populated when in popup mode, and a Portal/Endpoint has been selected.
  // A separate window will be opened, and credentials & metadata from that connection will be passed to this callback fn.
  // SEE: https://docs.connect.fastenhealth.com/stitch/webcomponent for example data structure.
  // {
  //   "org_connection_id": "1234-1234-1234-1234-1234",
  //   "endpoint_id": "1234-1234-1234-1234-1234"
  // }
  public orgConnectionCallbackSubject = new BehaviorSubject<OrganizationConnectionCallbackEvent | null>(null)

  // this subject is populated when the "page" is changed
  public componentNavigationSubject = new BehaviorSubject<ComponentNavigationEvent | null>(null)

  constructor() { }

  reset(){
    this.componentNavigationSubject.next(null)
  }
}
