import {ComponentNavPage, ConnectMode} from '../constants';
import {ComponentNavigationEvent} from '../models/component-navigation-event';
import {ConnectService} from '../services/connect.service';
import {MessageBusService} from '../services/message-bus.service';
import {Observable} from 'rxjs';

export interface ConnectData {
  public_id: string;
  catalog_brand_id: string;
  catalog_portal_id: string;
  catalog_endpoint_id: string;


  //only populated when reconnect_org_connection is present
  org_connection_id?: string;
  external_id?: string
  external_state?: string
  connect_mode?: ConnectMode
  popup_timeout_mins?: number
}

export function ConnectHelper(connectApi:ConnectService, messageBus: MessageBusService, connectData: ConnectData): void {
  connectApi.connectWithRedirect(connectData.public_id, connectData.catalog_brand_id, connectData.catalog_portal_id, connectData.catalog_endpoint_id, connectData.org_connection_id, connectData.connect_mode, connectData.external_id, connectData.external_state, connectData.popup_timeout_mins).subscribe((orgConnectionCallbackData) => {
    console.log(orgConnectionCallbackData)

    if(!orgConnectionCallbackData){
      return //wait for redirect
    }

    //Note: this code will only run when this.connectMode == popup
    //when connectMode == 'redirect', the user is redirected in the same window, and this code is never executed.

    messageBus.orgConnectionCallbackSubject.next(orgConnectionCallbackData)

    //redirect the browser back to the search page (TODO: this should show the support request page if an error occurred)
    messageBus.componentNavigationSubject.next({
      page: ComponentNavPage.Search,
    } as ComponentNavigationEvent)
  })
}
