import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {catchError, concatMap, filter, first, fromEvent, map, Observable, of, throwError, timeout} from 'rxjs';
import {ResponseWrapper} from '../models/response-wrapper';
import {environment} from '../../environments/environment';
import {Organization} from '../models/organization';
import {ApiMode, ConnectMode} from '../constants';
import {OrganizationConnectionCallbackEvent} from '../models/organization-connection-callback-event';
import {SearchFilter} from '../models/search-filter';
import {LighthouseSourceSearch} from '../models/lighthouse/lighthouse-source-search';
import {FormSupportRequest} from '../models/forms/form-support-request';
import {FormRequestHealthSystem} from '../models/forms/form-request-health-system';
import {OrganizationConnection} from '../models/organization-connection';

@Injectable({
  providedIn: 'root'
})
export class ConnectService {

  constructor(
    private _httpClient: HttpClient,
  ) { }

  public getOrgByPublicId(publicId: String): Observable<Organization> {
    let queryParams = {}
    queryParams['public_id'] = publicId

    return this._httpClient.get<ResponseWrapper>(
      `${environment.connect_api_endpoint_base}/bridge/org`, {params: queryParams})
      .pipe(
        map((response: ResponseWrapper) => {
          console.log("Organization", response)
          return response.data as Organization;
        })
      );
  }

  public getOrgConnectionById(publicId: String, orgConnectionId: String): Observable<OrganizationConnection> {
    let queryParams = {}
    queryParams['public_id'] = publicId
    return this._httpClient.get<ResponseWrapper>(
      `${environment.connect_api_endpoint_base}/bridge/org_connection/${orgConnectionId}`, {params: queryParams})
      .pipe(
        map((response: ResponseWrapper) => {
          console.log("Organization Connection Data", response)
          return response.data as OrganizationConnection;
        })
      );
  }

  public searchCatalogBrands(apiMode: ApiMode, filter: SearchFilter): Observable<LighthouseSourceSearch> {
    if((typeof filter.searchAfter === 'string' || filter.searchAfter instanceof String) && (filter.searchAfter as string).length > 0){
      filter.searchAfter = (filter.searchAfter as string).split(',')
    } else {
      filter.searchAfter = []
    }
    const endpointUrl = new URL(`${environment.connect_api_endpoint_base}/bridge/catalog/search`);

    return this._httpClient.post<ResponseWrapper>(endpointUrl.toString(), filter, {params: {
        'api_mode': apiMode
      }})
      .pipe(
        map((response: ResponseWrapper) => {
          console.log("Metadata RESPONSE", response)
          return response.data as LighthouseSourceSearch
        })
      );
  }

  supportRequest(request: FormSupportRequest): Observable<any> {
    const endpointUrl = new URL(`${environment.connect_api_endpoint_base}/support/request`);

    return this._httpClient.post<any>(endpointUrl.toString(), request)
      .pipe(
        map((response: ResponseWrapper) => {
          // @ts-ignore
          return {}
        })
      );
  }

  requestHealthSystem(requestHealth: FormRequestHealthSystem): Observable<any> {
    const endpointUrl = new URL(`${environment.connect_api_endpoint_base}/support/healthsystem`);

    return this._httpClient.post<any>(endpointUrl.toString(), requestHealth)
      .pipe(
        map((response: ResponseWrapper) => {
          // @ts-ignore
          return {}
        })
      );
  }

  public connectWithRedirect(publicId: string, brandId: string, portalId: string, endpointId: string, reconnectOrgConnectionId?: string, connectMode?: ConnectMode, externalId?: string, externalState?: string, popupTimeoutMins?: number): Observable<OrganizationConnectionCallbackEvent> {
    const redirectUrlParts = new URL(`${environment.connect_api_endpoint_base}/bridge/connect`);
    const redirectParams = new URLSearchParams()
    redirectParams.set("public_id", publicId)
    redirectParams.set("brand_id", brandId)
    redirectParams.set("portal_id", portalId)
    redirectParams.set("endpoint_id", endpointId)
    if (reconnectOrgConnectionId) {
      redirectParams.set("reconnect_org_connection_id", reconnectOrgConnectionId)
    }
    if (connectMode) {
      redirectParams.set("connect_mode", connectMode)
    }
    if (externalId) {
      redirectParams.set("external_id", externalId)
    }
    if (externalState) {
      redirectParams.set("external_state", externalState)
    }

    redirectUrlParts.search = redirectParams.toString()
    console.log(redirectUrlParts.toString());


    //if we're in popup mode, we can open a new window, rather than redirecting the current window (which is an app frame)
    if(connectMode == ConnectMode.Popup) {

      //open a external url in a new window
      let openedWindow = window.open(redirectUrlParts.toString(), "_blank");

      if (!popupTimeoutMins) {
        popupTimeoutMins = 30 //wait 30 mins max before closing the window
      }

      return this.waitForOrgConnectionOrTimeout(openedWindow, popupTimeoutMins)
    }
    else {
      //redirect to the url in the same window
      window.location.href = redirectUrlParts.toString();
      return of(null) //should never happen
    }
  }


  //This function must be ".subscribed()" to work. If not, the handler will not be registered and messages will be ignored.
  private waitForOrgConnectionOrTimeout(openedWindow: Window, popupTimeoutMins: number): Observable<OrganizationConnectionCallbackEvent> {
    console.log(`waiting for postMessage notification from popup window`, popupTimeoutMins)

    //new code to listen to post message
    return fromEvent(window, 'message')
      .pipe(
        //throw an error if we wait more than 2 minutes (this will close the window)
        timeout(popupTimeoutMins * 60*1000), //popup timeout (mins) * 60 seconds * 1000 ms
        //make sure we're only listening to events from the "opened" window.
        filter((event: MessageEvent) => event.source == openedWindow),
        //after filtering, we should only have one event to handle.
        first(),
        map((event) => {
          console.log(`received postMessage notification from popup window & sending acknowledgment`)
          // @ts-ignore
          event.source.postMessage(JSON.stringify({close:true}), event.origin);
          console.log("postmessage data", event.data)
          return JSON.parse(event.data) as OrganizationConnectionCallbackEvent
        }),
        catchError((err) => {
          console.warn(`timed out waiting for notification from popup (${popupTimeoutMins} mins), closing window`)
          openedWindow.self.close()
          return throwError(err)
        })
      )
  }

}
