import {Component, Input, OnChanges, OnInit, SimpleChanges, ViewChild} from '@angular/core';
import {NavOutletDirective} from './nav-outlet.directive';
import {MessageBusService} from '../../services/message-bus.service';
import {ComponentNavigationEvent} from '../../models/component-navigation-event';
import {ApiMode, ComponentNavPage, ConnectMode} from '../../constants';
import {BrandSearchComponent} from '../../components/brand-search/brand-search.component';
import {PortalConnectComponent} from '../../components/portal-connect/portal-connect.component';
import {Organization} from '../../models/organization';
import {FormSupportRequestComponent} from '../../components/form-support-request/form-support-request.component';
import {
  FormRequestHealthSystemComponent
} from '../../components/form-request-health-system/form-request-health-system.component';

@Component({
  selector: 'app-nav-outlet',
  templateUrl: './nav-outlet.component.html',
  styleUrls: ['./nav-outlet.component.css']
})
export class NavOutletComponent implements OnInit, OnChanges {
  @Input() apiMode!: ApiMode;
  @Input() org!: Organization;
  @Input() publicId!: string
  @Input() connectMode!: ConnectMode;
  @Input() reconnectOrgConnectionId: string | null = null;
  @Input() externalId: string | null = null; //optional opaque id to pass to the callback
  @Input() externalState: string | null = null; //optional opaque state to pass to the callback
  @Input() popupTimeout: number | null = null; //optional popup timeout in minutes

  //location to dynamically load the resource list
  @ViewChild(NavOutletDirective, {static: true}) navOutlet!: NavOutletDirective;

  constructor(private messageBus: MessageBusService) { }

  ngOnInit(): void {
    this.loadComponent()

    this.messageBus.componentNavigationSubject.subscribe((componentNavEvent) => {
      console.log("bubbling up event", componentNavEvent)
      this.loadComponent(componentNavEvent)
    })
  }
  ngOnChanges(changes: SimpleChanges) {
    this.loadComponent()
  }

  loadComponent(componentNavEvent?: ComponentNavigationEvent) {
    //clear the current outlet
    const viewContainerRef = this.navOutlet.viewContainerRef;
    viewContainerRef.clear();

    let page = componentNavEvent?.page || ComponentNavPage.Search

    if(page == ComponentNavPage.Search){
      const componentRef = viewContainerRef.createComponent(BrandSearchComponent);
      componentRef.instance.apiMode = this.apiMode;

      componentRef.instance.markForCheck()
    } else if(page == ComponentNavPage.PortalConnect) {
      const componentRef = viewContainerRef.createComponent(PortalConnectComponent);
      componentRef.instance.apiMode = this.apiMode;
      componentRef.instance.org = this.org;
      componentRef.instance.publicId = this.publicId;
      componentRef.instance.connectMode = this.connectMode;
      componentRef.instance.reconnectOrgConnectionId = this.reconnectOrgConnectionId;
      componentRef.instance.externalId = this.externalId;
      componentRef.instance.externalState = this.externalState;
      componentRef.instance.popupTimeout = this.popupTimeout;

      componentRef.instance.brand = componentNavEvent.data;
      componentRef.instance.markForCheck()
    } else if(page == ComponentNavPage.FormSupportRequest){
      const componentRef = viewContainerRef.createComponent(FormSupportRequestComponent);
      componentRef.instance.markForCheck()
    } else if(page == ComponentNavPage.FormRequestHealthSystem){
      const componentRef = viewContainerRef.createComponent(FormRequestHealthSystemComponent);
      componentRef.instance.healthSystemName = componentNavEvent.data
      componentRef.instance.markForCheck()
    }
  }
}
