import {ChangeDetectorRef, Component, Input, OnInit} from '@angular/core';
import {SearchFilter} from '../../models/search-filter';
import {
  LighthouseBrandListDisplayItem,
  LighthouseSourceSearch,
  LighthouseSourceSearchAggregation
} from '../../models/lighthouse/lighthouse-source-search';
import {SourceListItem} from '../../fasten-stitch/fasten-stitch.component';
import {Observable, of} from 'rxjs';
import {ApiMode, ComponentNavPage} from '../../constants';
import {MessageBusService} from '../../services/message-bus.service';
import {ConnectService} from '../../services/connect.service';
import {ComponentNavigationEvent} from '../../models/component-navigation-event';

@Component({
  selector: 'app-brand-search',
  templateUrl: './brand-search.component.html',
  styleUrls: ['./../../fasten-stitch/fasten-stitch.component.css'],
  // encapsulation: ViewEncapsulation.Emulated

})
export class BrandSearchComponent implements OnInit {
  @Input() apiMode: ApiMode = ApiMode.Test;

  loading = false;

  lighthouseBrandList: SourceListItem[] = []

  filter: SearchFilter = new SearchFilter()

  //limits that are tied to the current result set.
  resultLimits: {
    totalItems: number,
    scrollComplete: boolean,
    platformTypesBuckets: LighthouseSourceSearchAggregation | undefined,
    categoryBuckets: LighthouseSourceSearchAggregation | undefined,
  } = {
    totalItems: 0,
    scrollComplete: false,
    platformTypesBuckets: undefined,
    categoryBuckets: undefined
  }

  constructor(
    public changeRef: ChangeDetectorRef,
    public connectApi: ConnectService,
    public messageBus: MessageBusService
  ) { }

  ngOnInit(): void {
    this.querySources(true)
  }

  markForCheck(){
    this.changeRef.markForCheck()
  }

  public querySources(reset: boolean): Observable<LighthouseSourceSearch|null> {
    if(reset){
      this.resetSearch()
    }

    console.log("querySources()", this.filter)
    if(this.loading){
      console.log("already loading, ignoring querySources()")
      return of(null)
    }
    //TODO: pass filter to function.
    // this.location.replaceState('/dashboard','', this.filter)

    if(!this.filter){
      this.filter = new SearchFilter()
      console.log("querySources() - no filter provided, using current form value", this.filter)
    }


    this.filter.fields = ["*"];
    this.loading = true
    var searchObservable = this.connectApi.searchCatalogBrands(this.apiMode, this.filter);
    searchObservable.subscribe(wrapper => {
        console.log("search sources", wrapper);
        // this.searchResults = wrapper.hits.hits;
        this.resultLimits.totalItems = wrapper.hits.total.value;

        this.lighthouseBrandList = this.lighthouseBrandList.concat(wrapper.hits.hits.map((result) => {
          return {
            brand: result._source,
            searchHighlights: result?.highlight?.aliases || []
          }
        }))

        //check if scroll is complete.
        if(!wrapper?.hits || !wrapper?.hits?.hits || wrapper?.hits?.hits?.length == 0 || wrapper?.hits?.total?.value == wrapper?.hits?.hits?.length){
          console.log("SCROLL_COMPLETE!@@@@@@@@")
          this.resultLimits.scrollComplete = true;
        } else {
          //change the current Page (but don't cause a new query)
          console.log("SETTING NEXT SORT KEY:", wrapper.hits.hits[wrapper.hits.hits.length - 1].sort.join(','))
          this.filter.searchAfter = wrapper.hits.hits[wrapper.hits.hits.length - 1].sort.join(",")
        }
        this.loading = false
      },
      error => {
        this.loading = false
        console.error("sources FAILED", error)
      },
      () => {
        this.loading = false
        console.log("sources finished")
      }
    );
    return searchObservable;
  }

  public selectBrand(brandItem: LighthouseBrandListDisplayItem){
    this.messageBus.componentNavigationSubject.next({
      page: ComponentNavPage.PortalConnect,
      title: brandItem.name,
      data: brandItem,
    } as ComponentNavigationEvent)
  }

  private resetSearch(){
    console.log("reset search...")
    this.lighthouseBrandList = []
    this.filter.searchAfter = []
    this.resultLimits = {
      totalItems: 0,
      scrollComplete: false,
      platformTypesBuckets: undefined,
      categoryBuckets: undefined
    }
  }

  onScroll() {
    if(!this.resultLimits.scrollComplete) {
      this.querySources(false)
    }
  }

  showRequestHealthSystem(event){
    this.messageBus.componentNavigationSubject.next({
      page: ComponentNavPage.FormRequestHealthSystem,
      title: 'Request your Health System',
      data: this.filter?.query,
    } as ComponentNavigationEvent)
  }

}
