import {
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  ViewEncapsulation,
  AfterViewInit,
} from '@angular/core';
import {initFlowbite, Modal} from 'flowbite';
import {LighthouseBrandListDisplayItem} from '../models/lighthouse/lighthouse-source-search';
import {ApiMode, ConnectMode} from '../constants';
import {Organization} from '../models/organization';
import {ConnectService} from '../services/connect.service';
import {MessageBusService} from '../services/message-bus.service';
import {OrganizationConnectionCallbackEvent} from '../models/organization-connection-callback-event';
import { ElementRef, ViewChild } from '@angular/core';
import {OrganizationConnection} from '../models/organization-connection';
import {ConnectHelper} from '../utils/connect-helper';

export class SourceListItem {
  brand: LighthouseBrandListDisplayItem
  searchHighlights?: string[]
}


@Component({
  // standalone: true,
  selector: 'fasten-stitch',
  templateUrl: './fasten-stitch.component.html',
  styleUrls: ['./fasten-stitch.component.css'],
  // SEE https://stackoverflow.com/questions/67115155/angular-web-component-how-to-embed-css-file-into-the-final-js-file
  // using view encapsulation to namespace CSS.
  encapsulation: ViewEncapsulation.Emulated, //default is  ViewEncapsulation.Emulated
  providers: [MessageBusService]
})
export class FastenStitchComponent implements OnInit, AfterViewInit {
  //NOTE: when adding fields here, we must also add them to the documentation
  @Input('public-id') publicId = ''; //validate
  @Input('connect-mode') connectMode: ConnectMode = ConnectMode.Redirect;
  @Input('reconnect-org-connection-id') reconnectOrgConnectionId: string | null = null;
  @Input('external-id') externalId: string | null = null; //optional opaque id to pass to the callback
  @Input('external-state') externalState: string | null = null; //optional opaque state to pass to the callback

  @Input('popup-timeout') popupTimeout: number = null; //optional timeout for popup mode, in minutes

  @Output('orgConnectionCallback') orgConnectionCallback = new EventEmitter<OrganizationConnectionCallbackEvent>()

  public apiMode: ApiMode = ApiMode.Test
  public org: Organization | undefined

  public errorMessage: string

  @ViewChild('stitchModalButton', { static: false }) public stitchModalButton: ElementRef;
  @ViewChild('stitchModal', { static: false }) public stitchModal: ElementRef;

  private flowbitStitchModal: Modal = null
  constructor(
    private host: ElementRef,
    private connectApi: ConnectService,
    public messageBus: MessageBusService,
  ) {

    //https://stackoverflow.com/a/69173549/1157633
    this.host.nativeElement.show = this.showStitchModalExt.bind(this);
    this.host.nativeElement.hide = this.hideStitchModalExt.bind(this);
  }

  ngAfterViewInit() {
    this.flowbitStitchModal = new Modal(this.stitchModal.nativeElement, {
      onHide: () => {
        this.messageBus.reset()
      },
    });
  }

  ngOnInit(): void {
    initFlowbite();

    let publicIdParts = this.publicId.split('_')
    if(publicIdParts.length == 3 && publicIdParts[1] == ApiMode.Live) {
      this.apiMode = ApiMode.Live
    }
    if(publicIdParts.length != 3){
      console.error("Could not register Fasten Connect installation: missing or invalid id", this.publicId)
      this.errorMessage = 'Could not register Fasten Connect installation: missing or invalid id. Please contact the developer of this app.'
      return
    }
    console.log(this.publicId, publicIdParts, this.apiMode)

    this.connectApi.getOrgByPublicId(this.publicId).subscribe((org: Organization) => {
      this.org = org
    },(err) => {
      this.errorMessage = 'Could not register Fasten Connect installation using id. Please contact the developer of this app.'
      console.log("Invalid Fasten Connect registration", err)
    })

    this.orgConnectionCallback.emit({
      error: "initializing-fasten-stitch",
    })

    this.messageBus.orgConnectionCallbackSubject.subscribe((orgConnectionCallbackData) => {
      if(!orgConnectionCallbackData) {
        console.warn("filter empty orgConnectionCallbackData")
        return
      }
      console.log("bubbling up event", orgConnectionCallbackData)
      this.orgConnectionCallback.emit(orgConnectionCallbackData)
    })
  }

  showStitchModal(): void {
    if(this.reconnectOrgConnectionId){
      const childSpan: HTMLElement = document.createElement('span');
      childSpan.innerHTML = 'Reconnecting...';
      (this.stitchModalButton.nativeElement as HTMLButtonElement).replaceChildren(childSpan); // Component needs to be added here

      this.connectApi.getOrgConnectionById(this.publicId, this.reconnectOrgConnectionId).subscribe((orgConnection) => {
        console.log("Reconnect Org Connection", orgConnection)

        ConnectHelper(this.connectApi, this.messageBus, {
          catalog_brand_id: orgConnection.catalog_brand_id,
          catalog_endpoint_id: orgConnection.catalog_endpoint_id,
          catalog_portal_id: orgConnection.catalog_portal_id,
          connect_mode: this.connectMode,
          external_id: this.externalId,
          external_state: this.externalState,
          org_connection_id: orgConnection.org_connection_id,
          public_id: this.publicId,
          popup_timeout_mins: this.popupTimeout,
        })

      }, (err) => {
        this.errorMessage = 'Could not find the patient connection using id. Please contact the developer of this app.'
        console.log("Invalid Fasten Connect Connection ID", err)
        this.flowbitStitchModal.show()
      })
    } else {
      this.flowbitStitchModal.show()
    }
  }

  // TODO: this is required because when using the showStitchModal() method, the Search component is not being rendered correctly
  // this function can be called externally to show the modal
  showStitchModalExt(): void {
    this.stitchModalButton.nativeElement.click()
  }

  hideStitchModalExt(): void {
    this.flowbitStitchModal.hide()
  }
}
