import {ChangeDetectorRef, Component, Input, OnInit} from '@angular/core';
import {ConnectService} from '../../services/connect.service';
import {FormRequestHealthSystem} from '../../models/forms/form-request-health-system';
import {ApiMode, ComponentNavPage} from '../../constants';
import {Message} from 'postcss';
import {MessageBusService} from '../../services/message-bus.service';
import {ComponentNavigationEvent} from '../../models/component-navigation-event';

@Component({
  selector: 'app-form-request-health-system',
  templateUrl: './form-request-health-system.component.html',
  styleUrls: ['./../../fasten-stitch/fasten-stitch.component.css'],
})
export class FormRequestHealthSystemComponent implements OnInit {
  @Input() healthSystemName: string = "";


  formRequestHealthSystem: FormRequestHealthSystem = null

  loading: boolean = false
  submitSuccess: boolean = false
  errorMsg: string = ""

  constructor(
    public changeRef: ChangeDetectorRef,
    private messageBus: MessageBusService,
    private connectServiceApi: ConnectService,
  ) { }

  ngOnInit(): void {
    this.resetForm()
  }

  markForCheck(){
    this.changeRef.markForCheck()
  }

  resetForm() {
    this.submitSuccess = false
    let requestForm = new FormRequestHealthSystem()
    requestForm.email = ''
    requestForm.name = this.healthSystemName
    requestForm.street_address = ''
    requestForm.website = ''

    this.formRequestHealthSystem = requestForm
  }

  submitForm() {
    this.loading = true
    this.connectServiceApi.requestHealthSystem(this.formRequestHealthSystem).subscribe((resp: any) => {
        this.loading = false
        this.submitSuccess = true
        //show success toast? close modal?
      },
      (err)=>{
        this.loading = false
        console.error("an error occurred during request submission",err)
        this.errorMsg = err || "An error occurred while submitting your request. Please try again later."
      })
  }

  dismiss(){
    this.messageBus.componentNavigationSubject.next({
      page: ComponentNavPage.Search,
    } as ComponentNavigationEvent)
  }
}
